import React, { FC, useState, useEffect } from 'react';
import { HelloWorld__factory, HelloWorld } from '../contracts/types';
import { CONTRACT_ADDRESS } from '../utils/constants';
import { ethers } from 'ethers';

const { ethereum } = window as any;
let helloWorld: HelloWorld;

type AccountContextType = {
  currentAccount: string;
  message: string;
  changeMessage: (msg: string) => void;
};

export const AccountContext = React.createContext<AccountContextType>({
  currentAccount: '',
  message: '',
  changeMessage: () => {},
});

export const AccountProvider: FC<{
  defaultCurrentAccount: string;
}> = ({ children, defaultCurrentAccount }) => {
  const [currentAccount, setCurrentAccount] = useState(defaultCurrentAccount);
  const [message, setMessage] = useState('');

  const requestAccounts = async () => {
    const accounts = await ethereum.request({ method: 'eth_requestAccounts' });
    setCurrentAccount(accounts[0]);
  };

  const getMessage = async () => {
    const msg = await helloWorld.getMessage();
    setMessage(msg);
  };

  const changeMessage = async (msg: string) => {
    await helloWorld.setMessage(msg).then(
      () => {
        setMessage(msg);
      },
      (reason) => {
        alert(reason.message);
      }
    );
  };

  useEffect(() => {
    async function load() {
      const provider = new ethers.providers.Web3Provider(ethereum);
      const signer = provider.getSigner();
      helloWorld = HelloWorld__factory.connect(CONTRACT_ADDRESS, signer);

      if (ethereum) {
        ethereum.on('accountsChanged', () => {
          requestAccounts();
        });
      }

      helloWorld.on('MessageChanged', (oldMessage, newMessage) => {
        // TODO: Exclude those changed by current account
        // if author != currentAccount {setMessage};
        setMessage(newMessage);
      });

      requestAccounts();
      getMessage();
    }

    load();
  }, []);

  return (
    <AccountContext.Provider value={{ currentAccount, message, changeMessage }}>
      {children}
    </AccountContext.Provider>
  );
};
